<template>
  <div class="team-detail" v-if="team!==undefined">
    <div :style="styleObject" class="team-top-container">
      <div class="top-left">
        <img :src="team.logo.mainName.svg" style="width: 100%" :alt="team.name">
      </div>
      <div class="top-right">
        <team-head-info label="名称" :text="team.name"></team-head-info>
        <team-head-info label="简称" :text="team.abbreviatedName"></team-head-info>
        <team-head-info label="排名" :text="team.placement"></team-head-info>
        <team-head-info label="地点" :text="team.location"></team-head-info>
        <team-head-info label="战绩" :text="team.records.matchWin+' - '+team.records.matchLoss"></team-head-info>
      </div>
    </div>
    <el-collapse :value="['recent','upcoming','players']">
      <el-collapse-item title="近期赛果" name="recent">
        <div class="recent-matches" v-if="team.matches.recent.length>0">
          <match-brief :match="match" v-for="match in team.matches.recent" :key="match.id" :prefix="true"
                       :suffix="true"></match-brief>
        </div>
      </el-collapse-item>
      <el-collapse-item title="赛事预告" name="upcoming">
        <div class="upcoming-matches" v-if="team.schedule.length>0">
          <match-brief :match="match" v-for="match in team.schedule" :key="match.id" :prefix="true"
                       :suffix="true"></match-brief>
        </div>

      </el-collapse-item>
      <el-collapse-item title="选手" name="players">
        <player-brief :player="player" v-for="player in team.players" :key="player.id"></player-brief>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
  import {isBright} from "@/common/util";
  import TeamHeadInfo from "@/components/Team/TeamHeadInfo";
  import MatchBrief from "@/components/Matches/MatchBrief";
  import PlayerBrief from "@/components/Team/PlayerBrief";

  export default {
    name: "Team",
    components: {PlayerBrief, MatchBrief, TeamHeadInfo},
    watch: {
      '$route'(to, from) {
        if (to.name === 'Team')
          this.fetchTeam();
      }
    },
    data() {
      return {
        team: undefined
      }
    },
    mounted() {
      this.fetchTeam()
    },
    methods: {
      fetchTeam() {
        this.$owl.getTeam(this.$route.params.id).then(team => {
            this.team = team;
          }
        )
      },
    },
    computed: {
      styleObject() {
        return {
          color: isBright(this.team.colors.primary.color.slice(1)) ? 'black' : 'white',
          backgroundColor: this.team.colors.primary.color
        }
      },

    },
  }
</script>

<style scoped>
  .team-top-container {
    padding: 0 10%;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }

  .top-left {
    padding: 20px;
    max-width: 300px
  }

  .top-right {
    padding: 20px;

  }

  @media (max-width: 700px) {
    .top-left {
      max-width: 10rem;
    }

    .team-top-container, .top-left, .top-right {
      padding: .4rem
    }
  }

  .recent-matches, .upcoming-matches {
    background-color: white;
    border: 1px solid rgba(51, 51, 51, .2);
    border-bottom: none;
  }

</style>
<style>
  .team-detail .el-collapse-item__content {
    background-color: #f8f8f8 !important;
    padding: 1rem 0.1rem;
    font-size:1rem;
  }

  .team-detail .el-collapse-item__header {
    background-color: #363636;
    color: white;
    font-weight: 700;
    font-size: 1.3rem;
    padding: 0 2rem;
  }

</style>



